body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: #706C61;
}

nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: end;
      justify-content: flex-end;
  background-color: #4286f4;
  padding: 20px 0;
}

nav a {
  text-decoration: none;
  color: white;
  margin: 0 40px;
}

nav a:hover {
  text-decoration: none;
}

nav a:active {
  text-decoration: none;
}

.container {
  width: 100%;
  height: 100%;
  padding: 2.5%;
  background-color: white;
  border-radius: 5px;
}

.carousel {
  padding: 0;
}

.carousel-inner .item {
  width: 100%;
}

#featured-artist {
  padding: 0 2.5%;
}


